import React, {Component} from "react";
import {OraLoader, OraSelect} from "eshop/components/OraCommonComponents";
import OfferDetailsPopUp from "eshop/modules/simfree/components/filter/OfferDetailsPopUp";
import ClientContextCheckbox from "eshop/modules/configurator/components/ClientContextCheckbox";
import Discount from "eshop/modules/configurator/components/stateless/Discount";
import OfferProductListMobileView from "eshop/modules/simfree/components/filter/view/OfferProductListMobileView"
import ProductListOfferPriceTag from "eshop/modules/simfree/components/filter/view/ProductListOfferPriceTag"
import {OraFloatingLabelSelect} from "eshop/components/OraFloatingLabelSelect";
import LabeledInput from "eshop/modules/core/components/hoc/LabeledInput";
import InfoWithTooltip from "eshop/modules/configurator/components/InfoWithTooltip";
import OfferDisclaimerComponent from "eshop/modules/simfree/components/OfferDisclaimerComponent";
import OfferType from "eshop/modules/simfree/constants/OfferTypeEnum";
import DiscountInfo from "eshop/modules/configurator/components/DiscountInfo";

let expanderCounter = 0;
const OfferDeviceFilterMobileView = (props) => {

    return (
        <div>
            <div
                className={props.hide ? "u-hide" : "l-page-row u-large-hide u-box-shadow u-no-padding-left u-no-padding-right"}>
                <OraLoader loading={props.loading} id="proposition-mobile-filter-loader" parentId="offer-filter-loader">

                    <div className="u-spacing-l">
                        <div className="g-gray1-bg">
                            <div className={"u-padding-top-l " + ((props.selectedOfferType === "SIMFREE_INST")
                                ? "u-no-padding-b " : "u-padding ")}>

                                <div className="l-row u-no-margin-right u-no-margin-left">
                                    {props.extProcessSelectConfig && createExtOfferSelect(props)}
                                    {!props.extProcessSelectConfig && <div>
                                        <div className="l-col l-col-small-12 l-col-medium-4 l-col-12 u-small-padding-left-l u-small-padding-right-l  u-medium-padding-left-l u-medium-padding-right-xs u-spacing-l">
                                            <OraFloatingLabelSelect
                                                id="processTypeFilterMobile"
                                                options={props.processTypesForSelect}
                                                onChange={props.selectProcessCallback}
                                                selected={props.selectedProcessValue}
                                                className="opl-input--size-m"
                                                label={props.processLabel}
                                                disabled={props.isDuet || !!props.addTerminalToOfferBundleNo ? true : undefined}
                                            />
                                        </div>
                                        <div className="l-col l-col-small-12 l-col-medium-4 l-col-12 u-small-padding-left-l u-small-padding-right-l  u-medium-padding-left-xs u-medium-padding-right-xs u-spacing-l">
                                            <OraFloatingLabelSelect
                                                id="loyaltyLengthFilterMobile"
                                                options={props.loyaltyLengthsForSelect}
                                                onChange={props.selectLoyaltyLengthCallback}
                                                selected={props.selectedLoyaltyLengthValue}
                                                className="opl-input--size-m"
                                                singleOptionClassName="u-spacing-l u-medium-inline-block u-medium-padding-left-xxl"
                                                label="Umowa"
                                                disabled={props.isDuet || !!props.addTerminalToOfferBundleNo ? true : undefined}
                                            />
                                        </div>
                                        <div className="l-col l-col-small-12 l-col-medium-4 l-col-12 u-small-padding-left-l u-small-padding-right-l u-medium-padding-left-xs u-medium-padding-right-l u-spacing-l">
                                            <OraFloatingLabelSelect
                                                id="deviceInstallmentsCountFilterMobile"
                                                options={props.deviceInstalmentsCountForSelect}
                                                onChange={props.selectDeviceInstalmentsCountCallback}
                                                selected={props.selectedDeviceInstalmentsCountValue}
                                                className="opl-input--size-m"
                                                singleOptionClassName="u-spacing-l u-medium-inline-block u-medium-padding-left-xxl"
                                                label="Liczba rat za telefon lub urządzenie"
                                            />
                                        </div>
                                    </div>}
                                    {(props.selectedOfferType !== "SIMFREE_INST") && (props.channels.sales === "WWW") &&
                                        <div className="l-col l-col-12 u-padding-left-l u-padding-right-l u-spacing-l u-medium-padding-left-m u-small-padding-left-l">
                                            <ClientContextCheckbox
                                                channels={props.channels}
                                                alignClass="u-small-left u-large-left" {...props.clientContextCheckboxConfig}
                                                labelClassName="u-spacing-l u-medium-block"
                                                addTerminalToOfferBundleNo={props.addTerminalToOfferBundleNo}
                                            />
                                        </div>
                                    }

                                    {props.info.label &&
                                        <div className="l-row u-padding-left u-padding-right">
                                            <div className="l-col l-col-12 g-yellow2-bg u-padding-all-l">
                                                <InfoWithTooltip {...props.info}/>
                                            </div>
                                        </div>
                                    }

                                    <div className="l-col l-col-12  u-padding-left-l u-padding-right-l">
                                        {(props.selectedOfferType !== "SIMFREE_INST") && !props.isB2B &&
                                            [OfferType.DATA].indexOf(props.selectedOfferType) === -1 ? (
                                            <OfferDisclaimerComponent descriptions={props.descriptions} processType={props.selectedProcessValue} />
                                        ) : (
                                            <div className={"o-separator " + ((props.selectedOfferType === "SIMFREE_INST")
                                                ? "u-small-hide u-medium-hide " : "")}></div>
                                        )}
                                    </div>

                                    {(props.selectedOfferType !== "SIMFREE_INST") && !props.isB2B
                                    && (<div className="l-col l-col-12  u-padding-left-l u-padding-right-l u-padding-all">
                                            <div className="u-border u-no-border-t u-no-border-l u-no-border-r u-padding-all">
                                                <DiscountInfo descriptions={props.descriptions} contractRole={props.contractRole} offerType={props.selectedOfferType}/>
                                            </div>
                                        </div>
                                        )
                                    }
                                </div>

                                {(props.selectedOfferType !== "SIMFREE_INST") &&
                                    <div  data-js-module="common/modules/opl-expander" data-js-options="{&quot;scrollToSelected&quot;: false, &quot;scrollTohash&quot;: true, &quot;hideOtherElements&quot;:true,&quot;triggerSelector&quot;:&quot;.opl-content-trigger&quot;,&quot;contentSelector&quot;:&quot;.opl-section__content&quot;,&quot;parentClass&quot;:&quot;opl-section--togglable&quot;, &quot;nextSelector&quot;:&quot;.opl-section__expander-next&quot;}">

                                                <div className="opl-section opl-section--togglable opl-section--inner-cut u-spacing-l">
                                                    <div className="l-row u-no-margin-right">
                                                        <div className="l-col">
                                                        <p className="u-font-bold u-padding-left-l u-margin-s">{props.selectedRatePlanName}</p>
                                                        <div className="g-brand2-c u-font-bold u-padding-left-l u-margin-s">
                                                            <ProductListOfferPriceTag
                                                                key={"key_" + props.selectedProcessValue + "_ProductListOfferPriceTag"}
                                                                proposition={props.selectedOfferObject}
                                                                entry={props.entry}
                                                                clientContext={props.clientContext}
                                                                contractRole={props.contractRole}
                                                                loyaltyLength={props.loyaltyLength}
                                                                mainPriceClass={"g-brand1-c"}
                                                                alignClass="u-left"
                                                                offerDiscountInfoDescription={props.descriptions.offerDiscountInfoDescription}
                                                                offerDiscountInfoConvDescription={props.descriptions.offerDiscountInfoConvDescription}
                                                                baseId="mobile"
                                                            />
                                                        </div>
                                                       </div>
                                                       </div>



                                                    {
                                                        props.selectedProcessValue === "MNP" && props.mnpPriceDescription ?
                                                             <Discount value={props.mnpPriceDescription} showAll={true}
                                                                      simpleWrapperClassName="u-padding-top u-padding u-padding-left-l u-padding-right-l g-yellowf-bg u-margin u-margin-top"/>

                                                            :
                                                            null
                                                    }

                                                    <div className="js-expander__container u-padding-left-l u-margin-s u-small-no-margin">
                                                        <div>
                                                            <div className="u-spacing-top-s u-small-no-spacing-top u-spacing-m">
                                                                {props.detailsTable ?
                                                                    <OfferDetailsPopUp
                                                                        key={"conv_" + props.clientContext + "_" + props.propositionName}
                                                                        labelText={props.detailsLabelText}
                                                                        header={props.header}
                                                                        propositionName={props.propositionName}
                                                                        details={props.detailsTable}
                                                                        proposition={props.selectedOfferObject}
                                                                        entry={props.entry}
                                                                        clientContext={props.clientContext}
                                                                    />
                                                                    :
                                                                    null
                                                                }
                                                                {!props.addTerminalToOfferBundleNo &&
                                                                <a href="#" role="tab" aria-expanded="false" className="opl-content-trigger u-inline-block opl-section__expander-trigger-btn u-float-none u-small-no-margin-top">
                                                                    <span className="opl-section__expander-trigger--show">Zmień</span>
                                                                    <span className="opl-section__expander-trigger--hide">Zwiń</span>
                                                                </a>
                                                                }
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div aria-expanded="false" className="opl-section__content">
                                                        <ul className="opl-multicheckout-services u-padding-left-m u-padding-right-m">
                                                            {props.offers && props.offers.map((offer, index) => <OfferProductListMobileView
                                                                offerDiscountInfoDescription={props.descriptions.offerDiscountInfoDescription}
                                                                offerDiscountInfoConvDescription={props.descriptions.offerDiscountInfoConvDescription}
                                                                isSelected={offer.id === props.selectedOffer} key={"key_mobile_" + props.selectedProcessValue + "_" + index}
                                                                index={index}
                                                                contractRole={props.contractRole}
                                                                selectedOfferType={props.selectedOfferType}
                                                                clientContext={props.clientContext} offer={offer}
                                                                loyaltyLength={props.loyaltyLength}
                                                                onChange={() => props.selectOfferCallback(offer.id)}/>)}
                                                        </ul>
                                                    </div>
                                                </div>

                                    </div>
                                }
                                            </div>
                        </div>
                    </div>
                </OraLoader>
            </div>

            <div className={props.channels.sales === 'WWW' ? "u-hide" : "opl-console u-margin-m u-padding-right-l u-large-hide"}>
                <label>{props.descriptions.searchByValueLabel || "Wyszukaj wg nazwy lub indeksu materiałowego:"}</label>
                <div className="opl-input-with-icon">
                    <LabeledInput className="g-white1-bg" onChange={props.handleChangeSearchDeviceValue} value={props.searchValue} id="search-by-value-mobile"/>
                    <span className="g-icon g-icon--only g-icon--s g-icon--search"></span>
                </div>
            </div>
        </div>
    )
}

const createExtOfferSelect = props => {
    let selectedExtOfferSelectIndexes = props.getSelectedExtProcessSelectIndexes();
    if (!selectedExtOfferSelectIndexes) {
        return null;
    }
    let lastLevel = selectedExtOfferSelectIndexes.length - 1;
    let extProcessSelect = props.getExtProcessSelectForLevel(lastLevel);
    let selectedOption = extProcessSelect.options[selectedExtOfferSelectIndexes[lastLevel]];
    return ([
        createExtProcessSelects(selectedExtOfferSelectIndexes, props),
        createLoyaltySelect(selectedOption, props),
        createInstallmentSelect(selectedOption, props)
    ])
};

const createExtProcessSelects = (selectedExtOfferSelectIndexes, props) => {
    return selectedExtOfferSelectIndexes.map((selectedExtOfferSelectIndex, level) => {
        let extProcessSelect = props.getExtProcessSelectForLevel(level);
        let isFirstLevel = level === 0;

            return (<div className={"l-col l-col-small-12 l-col-medium-4 l-col-12 u-small-padding-left-l u-small-padding-right-l  u-medium-padding-right-xs  u-spacing-l "+(!isFirstLevel ? "u-medium-padding-left-xs":"u-medium-padding-left-l")}>
            <OraFloatingLabelSelect
                id={`extProcessSelectLevel-${level}`}
                options={props.createOptionsForExtProcessSelect(extProcessSelect)}
                onChange={props.selectProcessForExtProcessSelect(extProcessSelect)}
                selected={selectedExtOfferSelectIndex}
                className="opl-input--size-m"
                label={extProcessSelect.label}/>
        </div>);
    })
};

const createLoyaltySelect = (selectedOption, props) => {
    if (selectedOption.showLoyalties) {
        return (<div className="l-col l-col-small-12 l-col-medium-4 l-col-12 u-small-padding-left-l u-small-padding-right-l u-medium-padding-left-xs u-medium-padding-right-xs u-spacing-l">
            <OraFloatingLabelSelect
                id="loyaltyLengthFilter"
                options={props.loyaltyLengthsForSelect}
                onChange={props.selectLoyaltyLengthCallback}
                selected={props.selectedLoyaltyLengthValue}
                className="opl-input--size-m"
                singleOptionClassName="u-spacing-l u-medium-inline-block u-medium-padding-left-xxl"
                label="Umowa"
                disabled={!!props.addTerminalToOfferBundleNo ? true : undefined}/>
        </div>);
    }
    return null;
};

const createInstallmentSelect = (selectedOption, props) => {
    if (selectedOption.showInstallments) {
        return (<div className="l-col l-col-small-12 l-col-medium-4 l-col-12 u-small-padding-left-l u-small-padding-right-l u-medium-padding-left-xs u-medium-padding-right-l u-spacing-l">
            <OraFloatingLabelSelect
                id="deviceInstallmentsCountFilter"
                options={props.deviceInstalmentsCountForSelect}
                onChange={props.selectDeviceInstalmentsCountCallback}
                selected={props.selectedDeviceInstalmentsCountValue}
                className="opl-input--size-m"
                singleOptionClassName="u-spacing-l u-medium-inline-block u-medium-padding-left-xxl"
                label="Liczba rat za telefon lub urządzenie"/>
        </div>);
    }
    return null;
};

export default OfferDeviceFilterMobileView;
